.card {
  background: #fff;
  border-radius: 10px;
  height: 200px;
  margin-bottom: 8px;
  position: relative;
  width: 200px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  align-content: center;
  text-align: center;
}

.card>.img-container {
  height: 100%;
  border-radius: 10px;
  overflow: hidden;
  background: lightgrey;
}

.card>.img-container>img {
  padding: 2px;
  width: 100%;
  height: 100%;
  border-radius: 10px;
}

.card:hover {
  color: white;
}

@media only screen and (max-width: 768px) {

  /* For mobile phones: */
  .card {
    height: 60px;
    margin: 4px;
    position: relative;
    width: 60px;
    align-content: center;
    text-align: center;
  }

  .card>.img-container>img {
    padding: 0px;
  }
}
.nav {
  width: 100%;
  height: auto;
  font-size: 16px;
  padding: 10px;
}

li {
  display: inline-block;
  font-size: 20px;
  padding: 20px;
}

.title, .score, .high-score {
  font-weight: bold;
  text-align: center;
  font-family: 'Source Sans Pro', sans-serif;
  letter-spacing: 2px;
  font-size: 24px;
}

.background {
  background-color: #026635;
  box-shadow: 0 4px 8px rgb(180, 180, 180), 0 4px 8px rgb(180, 180, 180);
  margin-bottom: 12px;
}

@media only screen and (max-width: 768px) {
  .score, .high-score {
    font-weight: bold;
    font-size: 20px;
  }
}

.modal-body {
  font-size: 20px;
}

.modal-title {
  text-align: center !important;
  font-weight: bolder;
  font-size: 26px;
}

h3 {
  font-family: "Source Sans Pro", sans-serif;
}

.content {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 50px;
  align-content: center;
}

@media only screen and (max-width: 768px) {
  .content {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
    align-content: center;
  }

  h3 {
    font-size: 20px;
    font-family: "Source Sans Pro", sans-serif;
  }

  .heading {
    font-size: 16px;
  }
}

